import axios from 'axios';
import errorService from './errorService';

//   axios   URL
const http = axios.create({
  baseURL: process.env.REACT_APP_API_URL || '/api',
  timeout: 15000,
  headers: {
    'Content-Type': 'application/json',
  }
});

//  
http.interceptors.request.use(
  config => {
    //   ,   
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    
    //       
    const initData = localStorage.getItem('tg_init_data');
    if (initData) {
      config.headers['X-Telegram-Init-Data'] = initData;
    }
    
    return config;
  },
  error => {
    //     
    errorService.captureException(error, {
      tags: { component: 'httpService', type: 'request' }
    });
    return Promise.reject(error);
  }
);

//  
http.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    //       
    const errorData = {
      tags: { 
        component: 'httpService', 
        type: 'response'
      },
      extra: {
        url: error.config?.url,
        method: error.config?.method,
        status: error.response?.status
      }
    };
    
    //      
    if (error.response?.status !== 401) {
      errorService.captureException(error, errorData);
    }
    
    //      (401),        
    if (error.response && error.response.status === 401) {
      //        
      console.log('Unauthorized - token expired or invalid');
    }
    
    return Promise.reject(error);
  }
);

//     HTTP 
const httpService = {
  /**
   *  GET 
   * @param {string} url - URL  
   * @param {object} params -  
   * @param {object} options -    axios
   * @returns {Promise} - Promise   
   */
  get: (url, params = {}, options = {}) => {
    return http.get(url, { params, ...options });
  },
  
  /**
   *  POST 
   * @param {string} url - URL  
   * @param {object} data -   
   * @param {object} options -    axios
   * @returns {Promise} - Promise   
   */
  post: (url, data = {}, options = {}) => {
    return http.post(url, data, options);
  },
  
  /**
   *  PUT 
   * @param {string} url - URL  
   * @param {object} data -   
   * @param {object} options -    axios
   * @returns {Promise} - Promise   
   */
  put: (url, data = {}, options = {}) => {
    return http.put(url, data, options);
  },
  
  /**
   *  PATCH 
   * @param {string} url - URL  
   * @param {object} data -   
   * @param {object} options -    axios
   * @returns {Promise} - Promise   
   */
  patch: (url, data = {}, options = {}) => {
    return http.patch(url, data, options);
  },
  
  /**
   *  DELETE 
   * @param {string} url - URL  
   * @param {object} options -    axios
   * @returns {Promise} - Promise   
   */
  delete: (url, options = {}) => {
    return http.delete(url, options);
  }
};

export default httpService; 